.transactionsTable {
  width: 300px;
  background-color: white;
  border-radius: 10px;
  text-align: center;
  margin: auto;
  border-collapse: collapse;
  border: 1px solid whitesmoke;
}
.type {
  padding-left: 10px;
  text-align: initial;
}
th,
td {
  height: 30px;
  border: 1px solid whitesmoke;
}
tr:nth-child(2n) {
  background-color: whitesmoke;
}
thead {
  background-color: turquoise;
  color: white;
  font-weight: bold;
}
